/* Login Form */
.form-control:focus ~ .floating-label,
input.form-control:not([value=""]):not(:focus) ~ .floating-label,
.form-select:focus ~ .floating-label,
select.form-select:not([data-value=""]):not(:focus) ~ .floating-label,
textarea.form-control:not([data-value=""]):not(:focus) ~ .floating-label{
  top: -9px;
  left: 20px;
  font-size: 12px;
  opacity: 1;
  padding: 0 5px;
  background-color: white;
  border-radius: 6px;
}

.form-outline{
  position: relative;
}

.form-control, .form-select, .form-control:focus{
  color: var(--color-1);
}

.form-control, .form-select{
  font-size: 14px;
  width: 100%;
}

.floating-label, .date-label {
  pointer-events: none;
  position: absolute;
  top: 10px;
  left: 18px;
  transition: 0.2s ease all;
  font-size: 14px;
}

.date-label{
  color: var(--color-1);
  font-weight: 500;
  background-color: var(--bg-color-1);
}
/*Change text in autofill textbox*/
input:-webkit-autofill{
  -webkit-text-fill-color: var(--color-1);
}

/* Sign Up form */
/* Chrome, Safari, Edge, Opera */
input.hidden-arrows::-webkit-outer-spin-button,
input.hidden-arrows::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input.hidden-arrows[type=number] {
  -moz-appearance: textfield;
}

.validation-error{
  font-size: 0.7rem;
  color: red;
  height: 1.2rem;
  text-align: end;
}

.outline-red{
  border-color: red;
}

input[type="date"], input[type="date"]:focus{
  color: transparent;
}