/* Loading icon */
.loading-container{
    display: flex;
    height: 100vh;
    width: 100%;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index: 1200;
}

#spinner{
    height: 16rem;
    width: 16rem;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    font-size: 2rem;
    background-color: var(--bg-color-1);
    border: 1rem solid var(--bg-color-2);
    padding: 0;
    border-radius: 50%;
    transition: box-shadow, 1s;
    animation: zoom 0.6s;
}

#spinner>span{
    position: relative;
}

.loading-backdrop{
    position: fixed;
    z-index: 1050;
    width: 100vw;
    height: 100vh;
    background-color: rgb(100, 100, 100);
    opacity: 0.5;
}