.alert{
    height: fit-content;
    transition: 0.5s ease background-color;
    position: fixed;
    width: 90%;
    top: 0;
    left: 0;
    margin: 0 5%;
    pointer-events: none;
  }
  
.alert-msg{
    font-size: 0.8rem;
}

.alert-success, .alert-danger{
    z-index:1201;
    pointer-events: all;
}