.navbar{
    z-index: 100;
  }

#navbar-container{
    position: fixed;
    height: 2.7rem;
    bottom: 0;
    z-index: 1;
    box-shadow: 0px 0px 6px var(--shadow);
    transition: bottom 0.6s, top 0.6s;
    width: 100%;
}

#nav-bar{
    line-height: 1.5rem;
    display: flex;
    justify-content: space-around;
    background: var(--color-2);
}

#nav-bar a{
    text-decoration: none;
    color:var(--color-1);
    padding: 0.6rem;
    font-weight: 500;
}

#nav-bar a:hover{
    color: var(--bg-color-1);
}

@media screen and (orientation:landscape),
    screen and (min-width: 768){
    #navbar-container{
        top:0;
        width: fit-content;
        right:0;
    }
    #nav-bar a{
        margin: 0 1.1vw 0 1.1vw;
    }
}