.top-right-tag{
    padding: .375rem .75rem;
    position: absolute;
    right: 0;
    margin-right: 0.3rem;
    top: 0;
    margin-top: 0.3rem;
    padding: .25rem .5rem;
    font-size: .875rem;
    border: .2rem solid var(--bg-color-2);
    border-radius: .25rem;
    color: var(--bg-color-2);
    background-color: var(--bg-color-3);
    transition: color .15s;
}

.top-right-tag:hover{
    color: var(--bg-color-3);
    background-color: var(--bg-color-2);
}