:root {
  --color-1: rgb(100, 100, 100);
  --color-2: rgb(51, 51, 51);
  --color-3: rgb(15, 15, 15);
  --bg-color-1: white;
  --bg-color-2: rgb(200, 200, 200);
  --bg-color-3: rgb(100, 100, 100);
  --shadow1: rgba(120,120,120, 0.6);
}

.dark-mode{
  --color-1: rgb(200, 200, 200);
  --color-2: rgb(100, 100, 100);
  --color-3: rgb(51, 51, 51);
  --bg-color-1: rgb(15, 15, 15);
  --bg-color-2: rgb(70, 70, 70);
  --bg-color-3: rgb(90, 90, 90);
  --shadow1: rgba(0,0,0, 0.6);
}

*{
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin:0;
}

.App {
  font-family: 'Montserrat', sans-serif;
  color:var(--color-1);
  font-weight: 300;
  background-color: var(--bg-color-1);
}

a{
  text-decoration: none;
  color: var(--color-1);
}

b, strong {
  font-weight: 500;
}

.btn-purple{
  color: white;
  background-color: blueviolet;
  border-color: blueviolet;
}

